import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Grid,
    Typography
  } from "@mui/material"
  import axios from 'axios';
  import { useState } from "react"
  import { useForm } from "react-hook-form";
  import { color } from "@mui/system";
  import CustomerNames from "./CustomersNames";
  
  import Notification from '../../components/Notifications';
  
  
  function ExistingFacilities(props) {
   // const {insernewdata,setOpenPopup2} = props
    const { insertfacility, recordForEdit, setOpenPopup } = props
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const [amount, setAmount]=useState('')
    const [profitamount, setProfit]=useState('')
    const [total, setTotal]=useState('')
    const registerOptions = {
        FacilityType: { required: "Facility is required" },
        Amountdisbursed: { required: "Amount disbursed is required" },
        outstanding: { required: "Out standing is required" },
        profitrate: { required: "Profit rate is required" },
        totalprofit: { required: "Total profit is required" },
        maturitydate: { required: "Maturity date is required" },
        monthlyinstallment: { required: "Monthly installment is required" },
        Bank: { required: "Bank is required" },
        applicationid: { required: "Applicaiton is required" },
       
        }
    
    const handleCancel = () => {
        return setOpenPopup(false)
    }
  
    const handleAmount = (event) => {
      // 👇 Get input value from "event"
      setAmount(event.target.value);
    };
  
    const handleProfit = (event) => {
      // 👇 Get input value from "event"
      setTotal(event.target.value+amount);
  
  console.log(total)
    };
  
    function handleRegistration(values) {
        insertfacility(values)
    }
   
    function handleError(event) { }
    const label = { inputProps: { 'aria-label': 'Switch demo' } };
    return (
      <Container maxWidth="sm" sx={{ margin: 0 }}>
          <Box sx={{ padding: '10px', borderRadius: '10px' }}>
          <Stack direction="row" spacing={2}>
          <Box flex={2}>
          <TextField
                  id="applicationid"
                defaultValue={recordForEdit.id}
                  label="Application ID"
                  name="applicationid"
                  {...register('applicationid', registerOptions.applicationid)}
              />
        </Box>
        <Box flex={2}>
        <FormControl sx={{ width: '110px' }}>
            <InputLabel id="possitionId-label">Facility Type</InputLabel>
            <Select
                labelId="demo-simple-select-label"
                id="FacilityType"
                label="Facility Type"
                sx={{ width: '250px' }}
               
                {...register("FacilityType", { required: "Facility type is required." })}
            >
                <MenuItem value={'Murabaha'}>Murabaha</MenuItem>
                <MenuItem value={'Other'}>Other</MenuItem>
                <MenuItem value={'Loan'}>Loan</MenuItem>
            </Select>
           
        </FormControl>
        </Box>
           
            </Stack>
            <br/>
    
            <Stack direction="row" spacing={2}>
            <Box flex={2} >
            <TextField
                                  id="Amountdisbursed"
                                
                                  label="Amount disbursed"
                                  name="Amountdisbursed"
                                  {...register('Amountdisbursed', registerOptions.Amountdisbursed)}
                              />
    <Typography sx={{color:'red'}}>{errors.Amountdisbursed?.type === "required" && "Purpose is required"}</Typography>
            </Box>
  <Box flex={2}>
  <TextField
                          id="outstanding"
                          sx={{ width: '230px' }}
                          label="Outstanding"
                          name="outstanding"
                          onChange={handleAmount}
                          {...register('outstanding', registerOptions.outstanding)}
                      />
                        <Typography sx={{color:'red'}}>{errors.outstanding?.type === "required" && "Outstanding is required"}</Typography>
    </Box>
    
    </Stack>
  
    <br/>
    
    <Stack direction="row" spacing={2}>
    <Box flex={2} >
    <TextField
                          id="profitrate"
                        
                          label="Profit rate"
                          name="Profit rate"
                          {...register('profitrate', registerOptions.profitrate)}
                      />
    <Typography sx={{color:'red'}}>{errors.profitrate?.type === "required" && "Profit rate is required"}</Typography>
    </Box>
  <Box flex={2}>
  <TextField
                  id="totalprofit"
                  sx={{ width: '230px' }}
                  label="Total Profit"
                  name="totalprofit"
                  {...register('totalprofit', registerOptions.totalprofit)}
              />
                <Typography sx={{color:'red'}}>{errors.totalprofit?.type === "required" && "Total profit is required"}</Typography>
  </Box>
 
  </Stack>
  
  <br/>
    
    <Stack direction="row" spacing={2}>
    <Box flex={2} >
  <TextField
                  id="maturitydate"
                 
                  label="Maturity date"
                  name="maturitydate"
                  type="date"
                  sx={{ width: '230px' }}
                  {...register('maturitydate', registerOptions.maturitydate)}
              />
    <Typography sx={{color:'red'}}>{errors.maturitydate?.type === "required" && "Maturity date is required"}</Typography>
  </Box>
  <Box flex={2}>
  <TextField
                  id="monthlyinstallment"
                  sx={{ width: '230px' }}
                  label="Monthly installment"
                  defaultValue={total}
                  name="Monthly installment"
                  {...register('monthlyinstallment', registerOptions.monthlyinstallment)}
              />
                <Typography sx={{color:'red'}}>{errors.monthlyinstallment?.type === "required" && "Monthly installment is required"}</Typography>
  </Box>
  </Stack>
  <br/>
  <Stack direction="row" spacing={2}>
  <Box flex={2} >
  <TextField
                  id="Bank"
                
                  label="EAB OR Other bank"
                  name="Bank"
                  {...register('Bank', registerOptions.Bank)}
              />
    <Typography sx={{color:'red'}}>{errors.Bank?.type === "required" && "Monthly installment is required"}</Typography>
  </Box>
  </Stack>
<br/>
  
                      <Button variant="contained"
                          onClick={handleSubmit(handleRegistration, handleError)}
                      >Add Details</Button>
                      <Button onClick={handleCancel}>Cancel</Button>
              </Box>
      </Container>
     
    )
  }
  export default ExistingFacilities
  