
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
} from "@mui/material"
import { useForm } from "react-hook-form";
import { color } from "@mui/system";
import TextareaAutosize from '@mui/base/TextareaAutosize';
import React, { useState, useEffect } from "react";
import axios from "axios";
import Notifications from "../../components/Notifications";


export default function NewVote(props) {
    const { recordForEdit, setOpenPopup4, insertvote } = props
    const [image, setImage] = useState('')
    const [appid, setApid] = useState()
    const [filename, setFileName] = useState('')
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });
    const newDate = new Date()

    const handleImage = (e) => {
        console.log(e.target.files)

        setImage(e.target.files);

    }
    function handleId(e) {
        console.log(e.target.value)
        setApid(e.target.value)

    }
    const handleName = (e) => {

        console.log(e.target.value)

        setFileName(e.target.value);

    }
    const handleCancel = () => {
        return setOpenPopup4(false)
    }

    const registerOptions = {
        recommendation: { required: "Recommendation is required" },
        status: { required: "Please Select status is required" },
        id:0
    }

    function handleRegistration(values) {
        insertvote(values)
    }

    function handleError(event) { }
    const label = { inputProps: { 'aria-label': 'Switch demo' } };

    return (
        <Container maxWidth="lg" sx={{ margin: 0 }}>
            <TextareaAutosize
                aria-label="minimum height"

                name="id"
                id="id"
defaultValue={recordForEdit.id}
                {...register('id', registerOptions.id)}
            />
<br/>
            <TextareaAutosize
                aria-label="minimum height"
                minRows={3}
                placeholder="Recommendation / Comment"
                style={{ width: 400, height: 150 }}
                name="recommendation"
                id="recommendation"
                {...register('recommendation', registerOptions.recommendation)}
            />
            <br/>
            <Typography sx={{ color: 'red' }}>{errors.recommendation?.type === "required" && "Recommendation is required"}</Typography>
            <br />
            <br />

            <Box flex={2}>
                <FormControl sx={{ width: '170px' }}>
                    <InputLabel id="possitionId-label">Select status</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="status"
                        label="status"
                        sx={{ width: '238px' }}
                        {...register('status')}
                    >
                     
                        <MenuItem value="Approved">Approved</MenuItem>
                        <MenuItem value="Declined">Declined</MenuItem>
                    </Select>
                    <Typography sx={{ color: 'red' }}>{errors.status?.type === "required" && "Status is required"}</Typography>
                </FormControl>

            </Box>
            <br/>
            <Button variant="contained"
                onClick={handleSubmit(handleRegistration, handleError)}
            >Submit</Button>
            <Button onClick={handleCancel}>Cancel</Button>
            <br />
            <br />

        </Container>
    );
}

