import { useState, useEffect } from "react";
import axios from 'axios';
import {
    FormControl,
    Select,
    InputLabel,
    MenuItem,
    Typography
} from "@mui/material"

function CustomerNames(props) {
    const [position, setPosition] = useState([]);
    const [myOptions, setMyOptions] = useState([])
  const [customer, setCustomer]=useState('')
    const {defaultValue} = props
    console.log(defaultValue)

    const getCustomers = async () => {
        try {
            const data = await axios.get("https://localhost:7015/api/Customer");
            setMyOptions(data.data)
        }
        catch (e) {
            console.log(e);
        }
    }

    useEffect(() => {
        getCustomers();
    }, []);

    const customername = myOptions.map(pos => {
        return <MenuItem value={pos.id}>{pos.name}</MenuItem>
    })

    return (
        <FormControl sx={{ width: '170px' }}>
            <InputLabel id="possitionId-label">Select Customer</InputLabel>
            <Select
                labelId="demo-simple-select-label"
                id="possitionId"
                label="Position"
                sx={{ width: '238px' }}
                defaultValue={defaultValue}
                {...props.register('customerid', {required: true})}
            >
                {customername}
            </Select>
            <Typography sx={{color:'red'}}>{props.errors.customerid?.type === "required" && "Customer is required"}</Typography>
        </FormControl>
    )
}
export default CustomerNames