import {
  Button,
  Switch,
  InputLabel,
  MenuItem,
  FormControl,
  Select,
  TextField,
  autocompleteClasses,
  Box,
  Container,
  Stack,
  Grid,
  Typography
} from "@mui/material"
import axios from 'axios';
import { useState } from "react"
import { useForm } from "react-hook-form";
import { color } from "@mui/system";
import CustomerNames from "./CustomersNames";

import Notification from '../../components/Notifications';


function NewApplication(props) {
  const {insernewdata,setOpenPopup2} = props
  const { register, handleSubmit, formState: { errors } } = useForm({
      mode: "onBlur"
  });
  const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
  const [amount, setAmount]=useState('')
  const [profitamount, setProfit]=useState('')
  const [total, setTotal]=useState('')
  const registerOptions = {
      customerid: { required: "Name is required" },
      purpose: { required: "Purpose is required" },
      amount: { required: "Amount is required" },
      tenure: { required: "Tenure is required" },
      contribution: { required: "Contribution is required" },
      profitrate: { required: "Profit rate is required" },
      profitamount: { required: "profit Amount is required" },
      totalamount: { required: "Total Amount is required" },
      monthlyinstallment: { required: "Monthly Installment is required" },
      securitydetails: { required: "Security Details is required" },
      securitydescription: { required: "Description is required" },
      sourceofpayment: { required: "source of payment is required" },
      value: { required: "value is required" },
      condition:{ required: "Condition is required" },
      RM:{ required: "RM recommendation is required" },
     
      }
  
  const handleCancel = () => {
      return setOpenPopup2(false)
  }

  const handleAmount = (event) => {
    // 👇 Get input value from "event"
    setAmount(event.target.value);
  };

  const handleProfit = (event) => {
    // 👇 Get input value from "event"
    setTotal(event.target.value+amount);

console.log(total)
  };

  function handleRegistration(values) {
      insernewdata(values)
  }
 
  function handleError(event) { }
  const label = { inputProps: { 'aria-label': 'Switch demo' } };
  return (
    <Container maxWidth="lg" sx={{ margin: 0 }}>
        <Box sx={{ padding: '10px', borderRadius: '10px' }}>
        <Stack direction="row" spacing={2}>
        <Box flex={2}>
          <CustomerNames register={register} errors={errors}/>
          </Box>
          <Box flex={2} >
          <TextField
                                id="purpose"
                              
                                label="Purpose"
                                name="purpose"
                                {...register('purpose', registerOptions.purpose)}
                            />
  <Typography sx={{color:'red'}}>{errors.purpose?.type === "required" && "Purpose is required"}</Typography>
          </Box>
          <Box flex={2}>
<TextField
                        id="amount"
                        sx={{ width: '230px' }}
                        label="Amount"
                        name="amount"
                        onChange={handleAmount}
                        {...register('amount', registerOptions.amount)}
                    />
                      <Typography sx={{color:'red'}}>{errors.amount?.type === "required" && "Amount is required"}</Typography>
  </Box>
  <Box flex={2} >
  <TextField
                        id="tenure"
                      
                        label="Facility Period"
                        name="tenure"
                        {...register('tenure', registerOptions.tenure)}
                    />
  <Typography sx={{color:'red'}}>{errors.tenure?.type === "required" && "Tenure is required"}</Typography>
  </Box>
          </Stack>
  

  <br/>
  
  <Stack direction="row" spacing={2}>

<Box flex={2}>
<TextField
                id="profitrate"
                sx={{ width: '230px' }}
                label="Profit Rate"
                name="profitrate"
                {...register('profitrate', registerOptions.profitrate)}
            />
              <Typography sx={{color:'red'}}>{errors.profitrate?.type === "required" && "Profit rate is required"}</Typography>
</Box>
<Box flex={2} >
<TextField
                id="profitamount"
                onChange={handleProfit}
                label="Profit Amount"
                name="profitamount"
                {...register('profitamount', registerOptions.profitamount)}
            />
  <Typography sx={{color:'red'}}>{errors.profitamount?.type === "required" && "Profit Amount is required"}</Typography>
</Box>
<Box flex={2}>
<TextField
                id="totalamount"
                sx={{ width: '230px' }}
                label="Total Amount"
                defaultValue={total}
                name="totalamount"
                {...register('totalamount', registerOptions.totalamount)}
            />
              <Typography sx={{color:'red'}}>{errors.totalamount?.type === "required" && "Total Amount is required"}</Typography>
</Box>
<Box flex={2} >
<TextField
                id="monthlyinstallment"
              
                label="Monthly Installment"
                name="monthlyinstallment"
                {...register('monthlyinstallment', registerOptions.monthlyinstallment)}
            />
  <Typography sx={{color:'red'}}>{errors.monthlyinstallment?.type === "required" && "Monthly installment is required"}</Typography>
</Box>
</Stack>

<br/>
  
  <Stack direction="row" spacing={2}>

<Box flex={2}>
<TextField
                id="sourceofpayment"
                sx={{ width: '230px' }}
                label="Source of Payment"
                name="sourceofpayment"
                {...register('sourceofpayment', registerOptions.sourceofpayment)}
            />
               <Typography sx={{color:'red'}}>{errors.sourceofpayment?.type === "required" && "Source of Payment is required"}</Typography>
</Box>
<Box flex={2} >
<TextField
                id="securitydetails"
              
                label="Security details"
                name="securitydetails"
                {...register('securitydetails', registerOptions.securitydetails)}
            />
   <Typography sx={{color:'red'}}>{errors.securitydetails?.type === "required" && "Security detail is required"}</Typography>
</Box>
<Box flex={2}>
<TextField
                id="securitydescription"
                sx={{ width: '230px' }}
                label="Security description"
                name="securitydescription"
                {...register('securitydescription', registerOptions.securitydescription)}
            />
               <Typography sx={{color:'red'}}>{errors.securitydescription?.type === "required" && "Security description is required"}</Typography>
</Box>
<Box flex={2} >
<TextField
                id="value"
              
                label="Value"
                name="value"
                {...register('value', registerOptions.value)}
            />
   <Typography sx={{color:'red'}}>{errors.value?.type === "required" && "Value is required"}</Typography>
</Box>
</Stack>


<br/>
<Stack direction="row" spacing={1}>


<Box flex={2} >
<TextField
                id="contribution"
              
                label="Contribution"
                name="contribution"
                {...register('contribution', registerOptions.contribution)}
            />
   <Typography sx={{color:'red'}}>{errors.contribution?.type === "required" && "Contribution is required"}</Typography>
</Box>
<Box flex={2} >
<TextField
                id="condition"
                sx={{ width: '230px' }}
                label="Conditions/ Recommendations"
                name="condition"
                {...register('condition', registerOptions.condition)}
multiline
                rows={5}
            />
   <Typography sx={{color:'red'}}>{errors.condition?.type === "required" && "Conditions is required"}</Typography>

</Box>
<Box flex={2} >
<TextField
                id="rm"
                sx={{ width: '230px' }}
                label="RM recommendation"
                name="RM"
                {...register('RM', registerOptions.RM)}
multiline
                rows={5}
            />
   <Typography sx={{color:'red'}}>{errors.RM?.type === "required" && "RM is required"}</Typography>

</Box>
</Stack>
 <br />

 

                    <Button variant="contained"
                        onClick={handleSubmit(handleRegistration, handleError)}
                    >Submit Application</Button>
                    <Button onClick={handleCancel}>Cancel</Button>
            </Box>
    </Container>
   
  )
}
export default NewApplication
